.content {
  height: 100%;
  padding: 0;
  background: rgb(0,0,0);
background: -webkit-gradient(linear, left bottom, left top, color-stop(15%, rgba(0,0,0,1)), to(rgba(57,57,57,1)));
background: -webkit-linear-gradient(bottom, rgba(0,0,0,1) 15%, rgba(57,57,57,1) 100%);
background: -o-linear-gradient(bottom, rgba(0,0,0,1) 15%, rgba(57,57,57,1) 100%);
background: linear-gradient(0deg, rgba(0,0,0,1) 15%, rgba(57,57,57,1) 100%);
}



